"use client";

import React, { useState, useEffect } from "react";
import { useSearchParams } from "next/navigation";
import { Room } from "livekit-client";
import { LiveKitRoom, RoomAudioRenderer } from "@livekit/components-react";
import { ConnectionManager } from "../components/ConnectionManager";
import { InterviewLayout } from "../components/InterviewLayout";
import { useInterviewState } from "../hooks/useInterviewState";
import { useRoomConnection } from "../hooks/useRoomConnection";
import { useAuth } from "@/contexts/AuthContext";

interface RoomPageProps {
  params: {
    id: string;
    locale: string;
  };
}

export default function RoomPage({ params }: RoomPageProps) {
  const searchParams = useSearchParams();
  const [room] = useState(() => new Room());
  const { user } = useAuth();
  
  const {
    isConnected,
    isConnecting,
    error,
    needsPermission,
    participantName,
    companyInfo,
    jobInfo,
    handleStartInterview,
    updateConnectionState,
    updateError,
    updateJobData,
  } = useInterviewState();

  const { connectToRoom } = useRoomConnection({
    room,
    searchParams,
    onConnectionChange: updateConnectionState,
    onError: updateError,
    onJobDataUpdate: updateJobData,
    userId: user?.id,
    roomId: params.id, // Pass the room ID from URL
  });

  // Auto-connect when permissions are granted
  useEffect(() => {
    if (!needsPermission && !isConnected && !isConnecting && !error) {
      connectToRoom();
    }
  }, [needsPermission, isConnected, isConnecting, error, connectToRoom]);

  // Show connection manager for setup
  if (needsPermission || error || isConnecting || !isConnected) {
    return (
      <ConnectionManager
        needsPermission={needsPermission}
        error={error}
        isConnecting={isConnecting}
        isConnected={isConnected}
        onStartInterview={handleStartInterview}
      />
    );
  }

  // Show main interview interface when connected
  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 via-white to-blue-50/30 relative overflow-hidden">
      <LiveKitRoom room={room}>
        <InterviewLayout
          jobInfo={jobInfo}
          companyInfo={companyInfo}
          participantName={participantName}
        />
        <RoomAudioRenderer />
      </LiveKitRoom>
    </div>
  );
}